.section-about-achievements {
  display: flex;
  flex-direction: column;
}

.about-achievements-wrapper {
  padding-top: 35px;
}

.about-achievements {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 190px;
  padding: 45px 0;
  border-top: 2px solid $white;
  border-bottom: 2px solid $white;

  @media (max-width: $tablet900-width) {
    gap: 50px;
  }

  @media (max-width: $tablet-width) {
    gap: 25px;
  }
}

.about-achievements-item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-achievements-item-img {
  filter: contrast(0.7);
  width: 100%;
  height: 100%;
  max-width: 304px;
  max-height: 304px;
  object-fit: cover;
}
