.section-teacher-biography {
  display: grid;
  grid-template-columns: 1fr 680px;
  border-bottom: 2px solid $white;
  color: $white;

  @media (max-width: $desktop1300-width) {
    grid-template-columns: 1fr 550px;
  }

  @media (max-width: $tablet-width) {
    grid-template-columns: 1fr;
  }
}

.teacher-biography-image {
  padding: 20px 45px;
  border-bottom: 2px solid $white;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @media (max-width: $desktop1300-width) {
    padding: 15px;
  }

  @media (max-width: $tablet900-width) {
    grid-column: span 2;
    padding: 30px;
  }
}

.teacher-biography-films-wrapper {
  grid-row: span 2;
  border-left: 2px solid $white;
  padding: 30px;

  @media (max-width: $desktop1300-width) {
    padding: 15px;
  }

  @media (max-width: $tablet900-width) {
    grid-column: span 2;
    grid-row: span 1;
    border-left: none;
    padding: 30px;
  }
}

.teacher-biography-films-sliders {
  .course-teacher__slider {
    padding-top: 0;
    padding-bottom: 0;
    border: none;

    @media (max-width: $tablet-width) {
      padding: 0;
    }
  }

  @media (max-width: $desktop1300-width) {
    .course-teacher__slider-wrapper-item img {
      width: auto;
    }
  }

  @media (max-width: $desktop1300-width) {
    padding: 0 30px;
  }

  @media (max-width: $tablet-width) {
    padding: 0;
  }

  @media (max-width: $mobile480-width) {
    .course-teacher__slider-wrapper-item img {
      width: 100%;
    }
  }
}

.teacher-biography__title {
  @include fontface-medium;
  font-size: 16px;
  line-height: 19.36px;
}

.teacher-biography-info {
  padding: 30px;

  @media (max-width: $desktop1300-width) {
    padding: 15px;
  }

  @media (max-width: $tablet900-width) {
    grid-column: span 2;
    border-top: 2px solid $white;
    padding: 0 30px 30px;
  }
}
