.section-page-form {
  padding-top: 50px;
  padding-bottom: 120px;
  border-bottom: 2px solid $white;

  @media (max-width: $tablet900-width) {
    padding-bottom: 50px;
  }
}

.login-page,
.registration-page {
  .section-page-form {
    padding-bottom: 40px;

    @media (max-width: $tablet900-width) {
      padding-bottom: 0;
    }
  }
}

.page-form-content {
  background-color: $white;
  color: $black;
  display: flex;

  @media (max-width: $tablet900-width) {
    flex-direction: column;
  }
}

.registration-page .page-form-content,
.login-page .page-form-content {
  background-color: transparent;
  color: $white;

  .form-page__title {
    color: $white;
  }

  .field__input {
    color: $white;

    &::placeholder {
      text-transform: uppercase;
      color: $white;
    }
  }

  .field-group__item {
    border-bottom: 1px solid $white;
  }

  .field-group__item.field-checkbox {
    border-bottom: none;
    padding-top: 20px;

    input[type="checkbox"] {
      accent-color: $light-blue;
      width: 26px;
      height: 26px;
      cursor: pointer;
    }
  }

  .field__select {
    color: $white;
    background-color: $medium-black;
  }
}

.page-form-content__item {
  display: flex;
  flex-direction: column;
  width: 50%;

  &:last-child {
    border-left: 2px solid $black;
    padding: 50px 45px;
  }

  @media (max-width: $desktop1300-width) {
    padding-bottom: 20px;
  }

  @media (max-width: $tablet900-width) {
    width: 100%;

    &:last-child {
      padding-top: 20px;
      border-left: none;

      @media (max-width: $mobile480-width) {
        padding: 10px;
      }
    }
  }

  @media (max-width: $tablet-width) {
    padding: 0;
  }
}

.form-page__title {
  @include fontface-regular-pro;
  font-style: normal;
  color: $black;
  padding-left: 60px;
  padding-top: 50px;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 45px;

  @media (max-width: $desktop1300-width) {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 20px;
    padding-left: 40px;
  }

  @media (max-width: $tablet900-width) {
    font-size: 25px;
    line-height: 35px;
    padding-top: 30px;
  }

  @media (max-width: $mobile480-width) {
    padding: 20px;
    margin-bottom: 0;
  }
}

.page-form {
  padding-left: 60px;
  border-top: 2px solid $black;
  padding-right: 30px;
  padding-bottom: 20px;

  @media (max-width: $desktop1300-width) {
    padding-left: 40px;
  }

  @media (max-width: $tablet900-width) {
    padding-right: 40px;
  }

  @media (max-width: $mobile480-width) {
    padding: 0 20px 20px;
  }
}

.form__group {
  border: 0;
  padding: 0;
  margin: 0;
}

.field {
  display: flex;
  align-items: center;
  gap: 5px;

  span {
    @include fontface-medium;
    font-size: 16px;
    line-height: 19.36px;
  }
}

.field-group {
  margin-bottom: 40px;

  @media (max-width: $desktop1300-width) {
    margin-bottom: 20px;
  }
}

.field-group__list {
  display: flex;
  flex-direction: column;
  gap: 7px;
}

.field-group__item {
  border-bottom: 1px solid $black;
}

.field__input {
  @include fontface-medium;
  display: flex;
  width: 100%;
  border: none;
  background-color: transparent;
  outline: none;
  padding: 38px 0;
  font-size: 16px;
  line-height: 19.36px;
  color: $black;

  &::placeholder {
    text-transform: uppercase;
    color: $black;
  }

  @media (max-width: $desktop1300-width) {
    padding: 20px 0;
  }
}

.field-file {
  display: flex;
  align-items: center;
  padding: 38px 50px 38px 0;
  background-image: url("../img/field-file.png");
  background-position: 95% 50%;
  background-repeat: no-repeat;
  outline: none;

  @media (max-width: $desktop1300-width) {
    padding: 20px 40px 20px 0;
  }

  span {
    @include fontface-medium;
    font-size: 16px;
    line-height: 19.36px;
    color: $black;
  }

  input[type="file"] {
    display: none;
  }

  .field__input {
    cursor: pointer;
  }
}

.field__select {
  @include fontface-medium;
  display: flex;
  align-items: center;
  padding: 30px 0;
  border: none;
  outline: none;
  width: 100%;
  text-decoration: underline;
  font-size: 16px;
  line-height: 19.36px;
  color: $black;
  text-transform: uppercase;

  option {
    padding: 20px;
  }

  @media (max-width: $desktop1300-width) {
    padding: 20px 0;
  }
}

.form__additional {
  @include fontface-medium;
  font-size: 16px;
  line-height: 19.36px;
}

.page-form-content__item-image {
  img {
    width: 100%;
    height: 100%;
    filter: grayscale(100%);
  }
}

.registration-page,
.login-page {
  .page-form-content__item {
    &:last-child {
      border-left: 2px solid $black;
      padding: 0;
    }
  }
}

.registration-page {
  .page-form-content__item-image {
    img {
      filter: grayscale(0);
    }
  }
}
