.section-programm-course {
  display: flex;
  flex-direction: column;
  background-color: $white;
}

.heading-programm-course {
  display: flex;

  @media (max-width: $desktop1300-width) {
    flex-direction: column;
  }
}

.heading-programm-course__item {
  width: 50%;

  &:first-child {
    border-right: 2px solid $black;
  }

  @media (max-width: $desktop1300-width) {
    width: 100%;

    &:first-child {
      border-right: none;
    }
  }
}

.programm-course-title {
  @include fontface-regular-pro;
  font-size: 40px;
  line-height: 38.28px;
  text-transform: uppercase;
  padding: 50px 0 30px 105px;
  margin: 0;

  @media (max-width: $desktop1300-width) {
    padding: 30px 0 30px 50px;
  }

  @media (max-width: $mobile480-width) {
    padding: 20px;
    font-size: 30px;
    line-height: 33px;
  }
}

.programms-course-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  padding: 50px 70px;
  border-top: 2px solid $black;

  .section-text {
    @include fontface-regular;
  }

  @media (max-width: $tablet-width) {
    padding: 20px;
  }
}

.programms-course-list__item {
  padding: 40px 30px 20px;
  max-width: 408px;
  height: 588px;
  border: 1px solid $black;
  display: flex;
  flex-direction: column;

  @media (max-width: $tablet900-width) {
    height: auto;
    padding: 30px;
  }

  @media (max-width: $tablet-width) {
    padding: 10px;
  }
}

.programms-course-list__item-top {
  display: flex;
  flex-direction: column;
  margin-bottom: auto;
}

.programms-course-list__item-bottom {
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.programms-course-list__item-title {
  @include fontface-bold;
  font-size: 36px;
  line-height: 43.57px;
  letter-spacing: -0.02em;
  padding: 0;
  margin: 0 0 30px;
  text-transform: uppercase;

  @media (max-width: $tablet-width) {
    margin-bottom: 10px;
    font-size: 22px;
    line-height: 30px;
  }
}

.programms-course-list__item-img {
  width: 100%;
  height: 100%;
  max-width: 396px;
  max-height: 197px;
  object-fit: cover;
  margin-top: 25px;
  margin-bottom: 25px;
}

.programms-course-list__item-text {
  align-self: flex-end;
}
